<template>
<div class="container">
  <Switcher @toRoman="setArabic(false)" @toArabic="setArabic(true)"></Switcher>
  <h1 v-if="arabic">{{h1ToArabic}} numeral converter</h1>
  <h1 v-else>{{h1ToRoman}} numeral converter</h1>
<input v-model="input" placeholder="Enter value to be converted">
<Button text="convert" @convert="clickFun()"></Button>
<p>Result is {{output}}</p>
</div>
</template>

<script>
import Switcher from "./components/Switcher.vue"
import { toRoman } from "./business.js";
import { toArabic } from "./business.js";
import  Button  from "./components/Button";
export default {
  name: 'App',
  components: {
    Button,
    Switcher
  },
  data(){
    return{
      input:null,
      output:"",
      arabic:false,
      h1ToRoman:'Arabic to Roman',
      h1ToArabic:'Roman to Arabic'
    }
  },
  methods:{
    clickFun(){
      if(this.arabic)
        this.output = toArabic(this.input);
      else
        this.output = toRoman(this.input);
    },
    setArabic(newbool){
      this.arabic = newbool;
    }
  }
}
</script>

