var map = new Map();
    map.set(1000,"M");
    map.set(900,"CM");
    map.set(500,"D");
    map.set(400,"CD");
    map.set(100,"C");
    map.set(90,"XC");
    map.set(50,"L");
    map.set(40,"XL");
    map.set(10,"X");
    map.set(9,"IX");
    map.set(5,"V");
    map.set(4,"IV");
    map.set(1,"I");
export function toRoman(input) {
    var result="";
    map.forEach((roman,arabic) => {
        while(((input/arabic)>>0) > 0){
            result = result + roman;
            input-=arabic;
        }
    });
    if(input!==0){
        return '\'Illegal symbols detected!\''
    }
    return result;
}
export function toArabic(input) {
    var result = 0;
    map.forEach((roman, arabic) => {
        while(input.indexOf(roman) === 0){
            result+=arabic;
            input = input.substr(roman.length,input.length);
            console.log(input);
        }
    });
    if(input!==''){
        return '\'Illegal symbols detected!\''
    }
    return result;
}

