<template>
    <div class="container">
        <div :class="{arabic : arabic === true}" class="box"></div>
        <button :class="{selected : arabic === false}" @click="toRoman()">Arabic to Roman</button>
        <button :class="{selected : arabic === true}" @click="toArabic()">Roman to Arabic</button>
    </div>
</template>
<script>
export default {
    name:'Switcher',
    data(){
        return {
            arabic:false
        }
    },
    methods:{
        toRoman(){
            this.$emit('toRoman');
            this.arabic = false;
            // var box = document.getElementById('box');
            // box.classList.add('roman');
            // box.classList.remove('arabic');
            console.log('life!');
            
        },
        toArabic(){
            this.$emit('toArabic');
            this.arabic = true;
            // var box = document.getElementById('box');
            // box.classList.add('arabic');
            // box.classList.remove('roman');
            console.log('life!');
        }
    }
    
    
}
</script>
<style scoped>
div.arabic{
    left:auto;
    right: 0;
    background-position: right;
}
.selected{
    color: white;
}
.container{
    display: flex;
    margin: 0 auto;
    width: fit-content;
    border-radius: 8px;
    border: 1px solid #374c61;
    padding: 0.5rem 0.25rem;
    position: relative;
}
.box{
    position: absolute;
    top: 0;
    left: 0;
    width: calc(50% - 0.25rem);
    height: calc(100% - 0.5rem);
    background: rgb(6,50,122);
    background: linear-gradient(142deg, rgb(8, 60, 145) 0%, rgb(20, 158, 135) 100%);
    background-size: 200%;
    background-position: left;    
    margin: 0.25rem;
    border-radius: 8px;
    transition: all ease-in-out 1s;
}
button{
    border:none;
    background: transparent;
    z-index: 100;
    color: black;
    font-weight:bold;
    font-size: 0.7rem;
    font-family: Avenir, Helvetica, Arial, sans-serif;

}

</style>